import styled from 'styled-components';

export const PageWrapper = styled.div`
    background: #ebebeb;
    padding-top: 60px;
    min-height: 100vh;

    @media screen and (max-width: 990px){
        padding-top: 80px;
    }
`;

export const Wrapper = styled.div`
    column-count: 4;
    column-gap: 3em;
    padding: 0px 80px;

    @media screen and (max-width: 1200px){
        column-count: 3;
    }

    @media screen and (max-width: 965px){
        column-count: 2;
        padding: 0px 50px;
    }

    @media screen and (max-width: 585px){
        column-count: 1;
        padding: 0px 30px;
    }
`;

export const Loader = styled.div`
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 150px;
`;

export const LoaderText = styled.p`
    margin-top: 20px;
    font-weight: bold;
    font-size: 20px;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;

    &.active{
        opacity: 1;
        visibility: visible;
    }
`;

export const SearchBlock = styled.form`
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
`;

export const InputBlock = styled.div`
    background: #3f51b5;
    width: 325px;
    display: flex;
    justify-content: space-between;
    border-radius: 30px;
`;

export const SearchInput = styled.input`
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 0px;
    border-top-right-radius: 0px;
    padding-left: 20px;
    width: 100%;
    border: none;
    font-weight: bold;
    font-size: 20px;

    &::placeholder{
        font-weight: bold;
        font-size: 20px;
    }
    
`;

export const SearchButton = styled.button`
    border-radius: 30px;
    padding: 10px;
    border: none;
    font-weight: bold;
    font-size: 20px;
    background: transparent;
`;

export const SearchButtonImage = styled.img`
    width: 25px;
`;

export const YearsBlock =styled.div`
    display: flex;
    margin-top: 10px;
    align-items: center;
`;

export const Years =styled.p`
    font-size: 20px;
    margin-left: 20px;
`;