import React, { useState } from 'react';
import { Block, ImageBlock, Download, InfoBlock, Date, Description, DescriptionButton, Arrow, Image, TextBlock, TextBlockTop, TextBlockBottom, Heading, Location } from './styles';
import arrow from './download.svg';


export const PhotoBlock = ({ key, color, src, alt, heading, location, description, date, downloadLink }) => {
    const [activeClass, setActiveClass] = useState(false)

    return (
        <Block key={key} color={color}>
            <ImageBlock
                onMouseOver={() => setActiveClass(true)}
                onMouseOut={() => setActiveClass(false)}
            >
                <Image src={src} alt={alt} />
            </ImageBlock>

            <TextBlock>
                <InfoBlock>
                    <Download target="_blanc" href={downloadLink} className={activeClass ? "active" : ""}>
                        <Arrow src={arrow} alt="download button" />
                    </Download>
                    <div>
                        <Date>{date}</Date>
                        <Location>{location}</Location>
                    </div>
                </InfoBlock>
                <TextBlockTop>
                    <Heading>{heading}</Heading>
                </TextBlockTop>
                <TextBlockBottom>
                    <Description>{description}... <DescriptionButton>read more</DescriptionButton></Description>
                </TextBlockBottom>
            </TextBlock>
        </Block>
    )
}
