import styled from 'styled-components';

export const Block = styled.div`
    display: inline-block;
    width: 100%;
    box-sizing: border-box;
    box-shadow:  13px 13px 18px #dadada,
             -13px -13px 18px #ffffff;
    border-radius: 15px;
    margin: 25px 0 5px 0;
    background: ${props => props.color};
    transition: 0.15s;

    &:hover{
        box-shadow:  9px 9px 9px #d8d8d8,
             -9px -9px 9px #ffffff;
    }
`;

export const ImageBlock = styled.div`
    display: flex;
    justify-content: center;
    cursor: pointer;
    align-items: flex-start;
`;

export const Image = styled.img`
    width:100%;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
`;


export const TextBlock = styled.div`
    padding: 0 10px 10px 10px;
`;

export const TextBlockTop = styled.div`
    position: relative;
    display: flex;
    margin-top: -20px;
`;

export const Heading = styled.h2`
    font-weight: 300;
`;

export const Arrow = styled.img`
    width: 20px;
`;

export const Download = styled.a`
    transition: 0.1s;
    opacity: 0;
    visibility: hidden;
    
    &.active, &:hover{
        opacity: 1;
        visibility: visible;
    }

    @media screen and (max-width: 1024px){
        opacity: 1;
        visibility: visible;
    }
`;

export const InfoBlock = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
    transform: translateY(-33px);
`;

export const Date = styled.p`
    color: white;
    font-weight: bold;
    font-size: 11px;
    text-shadow: 0.5px 0.5px 1px #222222;
    text-align: right;

`;  

export const Location = styled.p`
    font-weight: bold;
    right: 0;
    font-size: 11px;
    color: white;
    text-shadow: 0.5px 0.5px 1px #222222;
`;

export const TextBlockBottom = styled.div`
    padding: 15px 10px 10px 10px;
    /* border-top: 1px solid black; */
`;

export const Description = styled.p`
    text-align: justify;
`;

export const DescriptionButton = styled.span`
    font-weight: bold;
    cursor: pointer;
    &:hover{
        text-decoration: underline;
    }
`;
